import React from "react";
import { useMemo } from "react";
import { FONT_COLORS, TFontColors } from "../../colors.const";
import { getClassName } from "../../tools/getClassName";

import './Title.scss';

type TitleProps = {
    children: React.ReactNode,
    as?: 'h1' | 'h2' | 'h3' | 'span',
    color?: TFontColors,
    className?: string,
    subtitle?: boolean,
}

const cn = getClassName('title');

export const Title = ({
  children,
  as,
  color = 'black',
  className,
  subtitle,
}: TitleProps) => {
  const Component = as ?? 'span';
  const style = useMemo(() => ({ color: FONT_COLORS[color] }), [color]);

  return <Component style={style} className={cn({ subtitle }, className)}>{children}</Component>
}