import * as React from "react"
import { COLORS } from "../colors.const"

import Layout from "components/layout"
import Seo from "components/seo"

import { Slide, Props as SlideProps } from "../components/Slide/Slide"

import {
  Main,
  ProgrammingLanguages,
  Overview,
  News,
  Partners,
  MasterClass,
  Blog,
} from '../views/index';

import { getClassName } from "../tools/getClassName"

import './index.scss'

const cn = getClassName('index');

const Content = [
  {
    slide: { className: cn('slide', { type: 'main' }) },
    content: <Main />,
  },
  {
    slide: { flexible: true, className: cn('slide') },
    content: <ProgrammingLanguages />,
  },
  {
    slide: { flexible: true, className: cn('slide') },
    content: <Overview />
  },
  {
    slide: { flexible: true, className: cn('slide') },
    content: <Partners />
  },
  {
    slide: { backgroundColor: COLORS.MIGOL_BLUE, className: cn('slide', { type: 'master-class' }) },
    content: <MasterClass />
  },
  {
    slide: { flexible: true, className: cn('slide') },
    content: <News />
  },
  {
    slide: { flexible: true, className: cn('slide', { type: 'blog' }) },
    content: <Blog />
  },
] as Array<{
  slide: SlideProps,
  content: React.ReactNode
}>


const IndexPage = () => (
  <Layout>
    {Content.map(({ slide, content }) => (
      <Slide {...slide}>
        {content}
      </Slide>
    ))}
  </Layout>
)

export const Head = () => (<>
  <Seo title="Home" description={undefined} children={undefined} />
</>)

export default IndexPage
