import React from "react";
import { getClassName } from "../../../tools/getClassName";
import { Title } from "../../../components/Title/Title";
import { Card } from "../../../components/Card/Card";

import { StaticImage } from "gatsby-plugin-image";
import { CardList } from "../../../components/CardList/CardList";

import './Partners.scss';
import { PartnerCard } from "./__Card/PartnersCard";

const cn = getClassName('partners');

const partners = [
  {
    title: 'Funlab',
    ImageNode: <StaticImage src={'./assets/Funlab.jpg'} alt='Funlab logo' />,
    description: 'Funlab description',
  },
  {
    title: 'Smile school',
    ImageNode: <StaticImage src={'./assets/SmileSchool.png'} alt='Smile school logo' />,
    description: 'Smile school description',
  },
]

export const Partners = (props: { className?: string }) => (
  <div className={cn(null, props.className)}>
    <Title subtitle color='light-grey'>Partners</Title>
    <div className={cn('content')}>
      <div className={cn('list-wrapper')}>
        <div className={cn('list')}>
          <div className={cn('list-content')}>
            {partners.map((partner) => (
              <PartnerCard
                className={cn('list-item')}
                {...partner}
              />
            ))}
          </div>
        </div>
      </div>
    </div>
  </div>
);
