import React from "react";

export const cloneElement = (elem: React.ReactNode, extraProps: Record<string, any>) => {
  if (elem && typeof elem === 'object' && ('type' in elem)) {
    const { props } = elem;

    const className = [props.className, extraProps.className].filter(Boolean).join(' ');

    return <elem.type
      {...props}
      className={className}>
      {props?.children}
    </elem.type>
  }

  return elem;
}