import React from "react";
import { getClassName } from "../../../tools/getClassName";
import { Title } from "../../../components/Title/Title";
import { StaticImage } from "gatsby-plugin-image";
import { CardList } from "../../../components/CardList/CardList";

import './News.scss';

const cn = getClassName('news');

const items = [
  { title: 'Arduino basics', ImageNode: <StaticImage src='./assets/Arduino.jpg' alt='' />, subtitle: 'Arduino projects' },
  { title: 'Scratch coding', ImageNode: <StaticImage src='./assets/Scratch_2.jpg' alt='' />, subtitle: 'Video games' },
  { title: 'Python intro', ImageNode: <StaticImage src='./assets/Robot.jpg' alt='' />, subtitle: 'Math' },
  { title: 'C programming language', ImageNode: <StaticImage src='./assets/C.jpg' alt='' />, subtitle: 'Arduino projects' },
]

export const News = (props: { className?: string }) => (
  <div className={cn(null, props.className)}>
    <Title subtitle color='light-grey'>Latest news</Title>
    <CardList className={cn('list')} items={items} />
  </div>
);