import React, { useMemo } from 'react';
import { getClassName } from '../../tools/getClassName';
import { FONT_COLORS, TFontColors } from "../../colors.const";

import './Text.scss';

export type Props = {
  children: React.ReactNode,
  as?: 'p' | 'span' | 'div',
  color: TFontColors,
  className?: string,
}

const cn = getClassName('text');

export const Text = ({ children, as, color, className }: Props) => {
  const Component = as || 'p';

  const style = useMemo(() => ({ color: FONT_COLORS[color] }), [color]);

  return <Component style={style} className={cn(null, className)}>{children}</Component>;
}