import React from "react";
import { COLORS } from "../../colors.const";
import { getClassName } from "../../tools/getClassName";

const cn = getClassName("slide");

import './Slide.scss';

export type Props = {
  className?: string,
  children: React.ReactNode,
  backgroundColor?: COLORS | string,
  flexible?: boolean,
};

export const Slide = ({
  className,
  children,
  backgroundColor = COLORS.HEART_OF_ICE,
  flexible
}: Props) => {
  const style = React.useMemo(() => ({ backgroundColor }), [backgroundColor]);

  return <div className={cn({ flexible }, className)} style={style}>
    {children}
  </div>
}
