import React from 'react';

import { getClassName } from 'tools/getClassName';
import { FONT_COLORS } from '../../../../colors.const';
import { Title } from '../../../../components/Title/Title';
import { Text } from '../../../../components/Text/Text';

import './PartnersCard.scss';
import { cloneElement } from '../../../../tools/cloneElement';

const cn = getClassName('partners-card');

export type Props = {
  className?: string;
  ImageNode: React.ReactNode;
  title: string;
  description?: string;
}

export const PartnerCard = ({
  className,
  title,
  description,
  ImageNode,
}: Props) => {
  return (
    <div className={cn(null, className)}>
      <div className={cn('logo')}>
        {cloneElement(ImageNode, { width: 256, height: 256, className: cn('image') })}
      </div>
      <div className={cn('text')}>
        <Title as='h3' color='grey' subtitle className={cn('title')}>{title}</Title>
        <Text color='light-grey'>{description}</Text>
      </div>
    </div>
  );
};
