import { Link } from 'gatsby';
import React from 'react';
import { Button } from '../../../../components/Button/Button';
import { Text } from '../../../../components/Text/Text';
import { Title } from '../../../../components/Title/Title';
import { getClassName } from '../../../../tools/getClassName';

import { COLORS, TFontColors } from '../../../../colors.const';

const cn = getClassName('blog-card');

import './BlogCard.scss';

export type Props = {
  className?: string;
  title: string;
  description?: string;
  link: {
    to: string;
    title?: string;
  },
  color?: COLORS.GREEN | COLORS.RED;
}

const textColor: Record<COLORS.GREEN | COLORS.RED, TFontColors> = {
  [COLORS.GREEN]: 'light-green',
  [COLORS.RED]: 'light-red',
}

export const BlogCard = ({
  className,
  title,
  description,
  link,
  color = COLORS.GREEN,
}: Props) => {
  const style = {
    backgroundColor: color,
  }

  return (
    <div style={style} className={cn()}>
      <Title subtitle color='white' className={cn('title')}>
        {title}
      </Title>
      <Text className={cn('description')} color='white'>
        {description}
      </Text>
      <div className={cn('link')}>
        <Link to={link.to} title={link.title}>
          <Button color='white'>
            <Text color={textColor[color]}>{link.title}</Text>
          </Button>
        </Link>
      </div>
    </div>
  );
}
