import React from 'react';
import { getClassName } from '../../tools/getClassName';
import { GatsbyImage, getImage } from "gatsby-plugin-image"
import { cloneElement } from '../../tools/cloneElement';
import { Text } from '../Text/Text';

export type Props = {
  title: string;
  subtitle: string;
  ImageNode: React.ReactNode;
  className?: string;
};

const cn = getClassName('card');

import './Card.scss';

export const Card: React.FC<Props> = ({ title, subtitle, ImageNode, className }) => {
  return (
    <div className={cn(null, className)}>
      <div className={cn('image-wrapper')}>
       {cloneElement(ImageNode, { className: cn('image') })}
      </div>
      <div className={cn('content')}>
        <Text color='grey' className={cn('title')}>{title}</Text>
        <Text color='light-grey' className={cn('subtitle')}>{subtitle}</Text>
      </div>
    </div>
  );
};

